@use 'base/variables' as v;@use 'base/mixins' as m;.tabs{  @include m.tablet{    display: flex;    gap: 3rem;  }      button{    background-color: v.$azul;    color: v.$blanco;    padding: 1rem;    border: none;    border-radius: 8px;    display: block;    width: 100%;    margin-bottom: 1rem;    text-transform: uppercase;    font-weight: v.$bold ;    transition: background-color 300ms ease;        &:hover{      cursor: pointer;      background-color: darken(v.$azul,10%);    }        &.actual{      background-color: v.$blanco;      color: v.$azul;    }      }}